import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { chooseSeat } from "../services/actions";
import cn from "classnames";

const SeatItem = ({ number, isSeat, seatInfo }) => {
   const [isSelected, setIsSelected] = useState(false);
   const { seats } = useSelector((state) => state.ticket);
   const { numberOfSeat } = useSelector((state) => state.userSelect);
   const dispatch = useDispatch();

   const handleSelect = () => {
      // X l vic chn gh  c t v yu cu c numberOfSeat
      if (!numberOfSeat) {
         alert("Please Enter your Name and Number of Seats");
         return;
      } else if (seatInfo.daDat) {
         alert("Ngi ta t ri");
         return;
      } else if (!isSeat) {
         alert("Bm lung tung!");
         return;
      }

      if (seats.length > 0 && seats.length === numberOfSeat) {
         const index = seats.findIndex((item) => item.soGhe === seatInfo.soGhe);

         if (index !== -1) {
            // Nu index tn ti => Cp nht li local state v dispatch ln action
            setIsSelected(!isSelected);
            dispatch(chooseSeat(seatInfo));
            return;
         } else {
            // Ngc li th gii hn s gh chn theo numberOfSeat
            alert(`Khng c chn hn ${numberOfSeat} ch !`);
            return;
         }
      }

      // Cp nht li local state v dispatch ln action
      dispatch(chooseSeat(seatInfo));
      setIsSelected(!isSelected);
   };

   // Reset li gh  chn
   useEffect(() => {
      if (!seats.length && isSelected) {
         setIsSelected(!isSelected);
      }
   }, [seats]);

   console.log("SeatItem redner", isSelected);

   return (
      <div
         className={cn(
            { seat: isSeat },
            { colNumber: !isSeat },
            { active: isSelected },
            { selected: seatInfo.daDat }
         )}
         onClick={handleSelect}
      >
         {number}
      </div>
   );
};

export default SeatItem;
